.FAQ {
  width: 90%;
  margin: auto;
  margin-top: 80px;
  padding-left: 40px;
  padding-bottom: 80px;
}
.FAQ h1 {
  font-size: 4rem;
  color: var(--primary);
}

.accordian .transition,
.accordian .artlist,
.accordian ul li i:before,
.accordian ul li i:after {
  transition: all 0.25s ease-in-out;
}

.accordian .no-select,
.accordian h2 {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.accordian h3 {
  letter-spacing: 1px;
  display: block;
  margin: 0;
  cursor: pointer;
  font-size: 2.7rem;
  color: white;
  padding-bottom: 6px;
}

.accordian .artlist {
  font-size: 17px;
  position: relative;
  overflow: hidden;
  z-index: 2;
  max-height: 800px;
  opacity: 1;
  transform: translate(0, 0);
  margin: 14px 0;
}
.accordian .artlist p {
  font-size: 1.8rem;
  color: gray;
}

.accordian ul {
  list-style: none !important;
  perspective: 900;
  padding: 0;
  margin: 0;
}

.accordian ul li {
  position: relative;
  padding: 0;
  margin: 0;
  padding: 10px 0;
  border-top: 1px solid #dce7eb;
}

.accordian ul li:nth-of-type(1) {
  animation-delay: 0.5s;
  border-top: none;
}

.accordian ul li:nth-of-type(2) {
  animation-delay: 0.75s;
}

.accordian ul li:nth-of-type(3) {
  animation-delay: 1s;
}

.accordian ul li:last-of-type {
  padding-bottom: 0;
}

.accordian ul li i {
  position: absolute;
  transform: translate(-6px, 0);
  margin-top: 3px;
  right: 0;
  font-size: 90rem !important;
}

.accordian ul li i:before,
ul li i:after {
  content: "";
  position: absolute;
  background-color: white;
  width: 3px;
  height: 9px;
}

.accordian ul li i:before {
  transform: translate(-2px, 0) rotate(45deg);
}

.accordian ul li i:after {
  transform: translate(2px, 0) rotate(-45deg);
}

.accordian ul li input[type="checkbox"] {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.accordian ul li input[type="checkbox"]:checked ~ .artlist {
  margin-top: 0;
  max-height: 0;
  opacity: 0;
  transform: translate(0, 50%);
  margin: 0;
}
.accordian ul li input[type="checkbox"]:checked ~ i:before {
  transform: translate(2px, 0) rotate(45deg);
}
.accordian ul li input[type="checkbox"]:checked ~ i:after {
  transform: translate(-2px, 0) rotate(-45deg);
}
.container {
  width: 100%;
  font-family: arial;
  font-size: 16px;
}
.center {
  text-align: center;
}

@media (max-width: 800px) {
  .FAQ {
    padding: 0 !important;
  }
  .FAQ .container h3 {
    font-size: 1.7rem;
  }
  .FAQ .container p {
    font-size: 1.4rem;
  }
  .FAQ
  {
      display: flex;
      flex-direction: column;
      align-items: center;
  }
}
