.header {
  background-color: var(--primary-act);
}
.header figure {
  margin-top: -10px !important;
  margin-left: 20px !important;
  cursor: pointer;
}
.header figure img {
  width: 130px;
  height: 120px;
}
.header figure figcaption {
  color: var(--primary);
  font-size: 2.5rem;
  margin-top: -45px;
  margin-left: 85px;
  font-weight: bold;
  /* border: 1px solid red; */
}
nav ul {
  margin-top: 55px;
}
nav ul li {
  margin-right: 20px;
  margin-top: 5px;
}
nav ul li a {
  border-radius: 20px;
  font-size: 1.8rem;
  transition: all 0.2s;
  color: white !important;
  transition: all 0.2s;
  padding: 2px 12px 2px 12px !important ;
}
nav ul li a:hover {
  background-color: var(--primary) !important;
  color: black !important;
}

nav ul button {
  padding: 4px 20px 4px 20px !important;
  margin-right: -10px;
  border-radius: 20px;
  font-size: 1.7rem;
  margin-top: 5px;
  transition: all 0.2s;
}
.loginBtn {
  background-color: gray !important;
  color: var(--primary-act);
  font-weight: 600;
  margin-right: 10px;
}
nav ul button:not(.england):hover,
nav ul button:not(.england):focus {
  background-color: var(--primary) !important;
}

.england {
  position: absolute;
  top: -45px;
  left: 15px;
  background-color: transparent;
  color: gainsboro;
  display: flex;
  justify-content: center;
  align-items: center;
}
.england img {
  width: 40px;
  height: 20px;
  margin-right: 5px;
}
.regBtn {
  color: var(--primary-act);
  font-weight: 600;
  background-color: grey !important;
}


