}
.swiper-container {
  width: 83.5%;
  height: 80vh;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* background: #fff; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero.swiper-slide {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.hero.swiper-slide:nth-child(1) {
  background-image: url(/task-01/static/media/car2.714f06a3.png);
}
.hero.swiper-slide:nth-child(2) {
  background-image: url(/task-01/static/media/car1.37adadb5.png);
}
.hero.swiper-slide:nth-child(3) {
  background-image: url(/task-01/static/media/car3.73769c21.png);
}
.hero.swiper-slide:nth-child(4) {
  background-image: url(/task-01/static/media/car4.7cd3e49e.png);
}
.hero.swiper-slide:nth-child(5) {
  background-image: url(/task-01/static/media/car5.30a6cff5.png);
}
.swiper-slide button {
  font-size: 2.6rem;
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 5px 20px 5px 20px;
  background-color: var(--primary);
  transition: all 0.2s;
  position: relative;
}
.swiper-slide button:hover {
  transform: scale(1.1);
  background-color: rgb(81, 194, 156);
}
.swiper-pagination {
  width: 100px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  z-index: 700;
  margin-left: 600px;
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
}
.swiper-pagination-bullet {
  padding: 5px;
  border: 2px solid white;
  outline: none;
}
.swiper-pagination-bullet-active {
  background: gray;
}


