}
.items-colorfull {
  width: 90%;
  height: 16px;
  border-radius: 10px;
  border: 1px solid white;
  margin-bottom: 15px;
  position: relative;
}
.items-colorfull::before {
  content: "";
  position: absolute;
  width: 80%;
  border-radius: 10px 0px 0 10px;
  height: 100%;
  background-color: var(--primary);
}
.items:nth-child(2) .items-colorfull::before {
  width: 70%;
  background-color: var(--primary);
}
.items:nth-child(3) .items-colorfull::before {
  width: 20%;
  background-color: var(--primary);
}
.items:nth-child(4) .items-colorfull::before {
  width: 100%;
  border-radius: 10px;
  background-color: var(--primary);
}
.items:nth-child(5) .items-colorfull::before {
  width: 60%;
  background-color: var(--primary);
}
.items:nth-child(6) .items-colorfull::before {
  width: 40%;
  background-color: var(--primary);
}
.items:nth-child(7) .items-colorfull::before {
  width: 80%;
  background-color: var(--primary);
}
.items:nth-child(8) .items-colorfull::before {
  width: 12%;
  background-color: var(--primary);
}
.items:nth-child(9) .items-colorfull::before {
  width: 80%;
  background-color: var(--primary);
}
.items:nth-child(10) .items-colorfull::before {
