  justify-content: center;
  align-items: center;
  margin-top: 50px;
}
.icon-logo figure {
  width: 100px;
  height: 120px;
}
.icon-logo img {
  width: 100%;
  height: 100%;
}

@media (min-width: 0px) and (max-width: 800px) {
  .section-4 {
    text-align: center;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .section-4 h1 {
    font-size: 3rem;
    margin-left: 10px !important;
  }
  .section-4 > .row > div:nth-child(2) {
    min-height: 50vh;
  }
  .section-4 > .row > div:nth-child(2) > .third-slider .clients {
    height: 100%;
  }
  .section-4 .stars i {
    font-size: 3rem;
    background-color: green;
    color: white;
    padding: 3px;
    margin-left: 5px;
  }
  .section-4 .trustpilot {
    padding: 5px;
    display: flex;
    align-items: center;
  }
  .section-4 .trustpilot i {
    font-size: 3rem;
    color: green;
  }
  .section-4 .trustpilot h5 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: white;
    font-size: 2rem;
  }
  .clients {
    display: flex;
    height: 30vh;
    flex-direction: column;
    justify-content: center !important;
    align-items: center !important;
    padding-left: 0px !important;
  }
  .clients .stars {
    padding: 0 !important;
  }
  .clients > .stars i {
    font-size: 1.9rem;
    padding: 3px;
  }
  .clients h2 {
    font-size: 2.5rem;
    color: white;
  }
  .clients p {
    color: white;
  }
  .clients .customer {
    display: none;
  }
}

.footer {
  padding-top: 1px;
  width: 90%;
  margin: auto;
  margin-top: 80px;
  /* background-color: red; */
}
.footer h1 {
  font-size: 4rem;
  color: var(--primary);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 35px;
}
.giftshop {
  width: 100%;
  min-height: 40vh;
  border: 1px solid gray;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px 0 0 0;
}
.giftshop > div {
  width: 25%;
  height: 35vh;
  transition: all .7;
}
.giftshop video {
  width: 100%;
  height: 100%;
  outline: none;
}
.giftshop figure {
  width: 100%;
  height: 100%;
}
.giftshop figure img {
  width: 100%;
  height: 100%;
}
.giftshop>div:hover
{
  transform: scale(1.07);
}
.footer > .btnn {
  display: flex;
  justify-content: center;
  padding: 60px 0;
}
.footer > .btnn > button {
  font-size: 2rem;
  padding: 5px 120px 5px 120px;
  border-radius: 20px;
  transition: all 0.2s;
}
.footer > .btnn > button:hover {
  background-color: var(--primary);
}
.footer .payment-method {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 10px;
}
.footer .payment-method h3 {
  font-size: 2.6rem;
  color: white;
}
.footer .payment-method div {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.footer .payment-method div figure {
  width: 80px;
  height: 40px;
  margin-left: 10px;
}
.footer .payment-method div figure img {
  width: 100%;
  height: 100%;
}
.copyright {
  margin-top: 50px;
  display: flex;
  justify-content: space-evenly;
  color: white;
}
.copyright p {
  font-size: 1.7rem;
  transition: all 0.2s;
}
.copyright p a {
  color: white;
}
.copyright p:hover,
.copyright p a:hover {
  color: var(--primary);
}

@media (min-width: 0px) and (max-width: 800px) {
  .footer {
    margin-top: 50px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer h1 {
    font-size: 3.3rem;
    margin-left: 12px;
  }
  .footer > .btnn > button {
    padding: 5px 80px 5px 80px;
  }
  .footer .payment-method h3 {
    font-size: 2.4rem;
  }
  .footer .payment-method div figure {
    width: 70px;
    margin: 0 0 0 5px;
  }
  .copyright {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .copyright p {
    font-size: 1.6rem;
  }
  .giftshop {
    flex-direction: column;
    justify-content: space-evenly;
  }
  .giftshop > div {
    width: 80%;
    margin-bottom: 70px;
  }
}

.FAQ {
  width: 90%;
  margin: auto;
  margin-top: 80px;
  padding-left: 40px;
  padding-bottom: 80px;
}
.FAQ h1 {
  font-size: 4rem;
  color: var(--primary);
}

.accordian .transition,
.accordian .artlist,
.accordian ul li i:before,
.accordian ul li i:after {
  transition: all 0.25s ease-in-out;
}

.accordian .no-select,
.accordian h2 {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.accordian h3 {
  letter-spacing: 1px;
  display: block;
  margin: 0;
  cursor: pointer;
  font-size: 2.7rem;
  color: white;
  padding-bottom: 6px;
}

.accordian .artlist {
  font-size: 17px;
  position: relative;
  overflow: hidden;
  z-index: 2;
  max-height: 800px;
  opacity: 1;
  transform: translate(0, 0);
  margin: 14px 0;
}
.accordian .artlist p {
  font-size: 1.8rem;
  color: gray;
}

.accordian ul {
  list-style: none !important;
  perspective: 900;
  padding: 0;
  margin: 0;
}

.accordian ul li {
  position: relative;
  padding: 0;
  margin: 0;
  padding: 10px 0;
  border-top: 1px solid #dce7eb;
}

.accordian ul li:nth-of-type(1) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  border-top: none;
}

.accordian ul li:nth-of-type(2) {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}

.accordian ul li:nth-of-type(3) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.accordian ul li:last-of-type {
  padding-bottom: 0;
}

.accordian ul li i {
  position: absolute;
  transform: translate(-6px, 0);
  margin-top: 3px;
  right: 0;
  font-size: 90rem !important;
}

.accordian ul li i:before,
ul li i:after {
  content: "";
  position: absolute;
  background-color: white;
  width: 3px;
  height: 9px;
}

.accordian ul li i:before {
  transform: translate(-2px, 0) rotate(45deg);
}

.accordian ul li i:after {
  transform: translate(2px, 0) rotate(-45deg);
}

.accordian ul li input[type="checkbox"] {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.accordian ul li input[type="checkbox"]:checked ~ .artlist {
  margin-top: 0;
  max-height: 0;
  opacity: 0;
  transform: translate(0, 50%);
  margin: 0;
}
.accordian ul li input[type="checkbox"]:checked ~ i:before {
  transform: translate(2px, 0) rotate(45deg);
}
.accordian ul li input[type="checkbox"]:checked ~ i:after {
  transform: translate(-2px, 0) rotate(-45deg);
}
.container {
  width: 100%;
  font-family: arial;
  font-size: 16px;
}
.center {
  text-align: center;
}

@media (max-width: 800px) {
