:root {
  --primary: #3cedb7;
  --secondary: aqua;
  --primary-act: #161413;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
::before,
::after {
  box-sizing: border-box;
}
button,
button:focus,
li:focus {
  border: none;
  outline: none;
}
html {
  font-size: 62.5%;
}

body {
  /* font-family: "Piazzolla", serif; */
  font-family: "Noto Sans JP", sans-serif;
  background-color: #161413;
  background-color: var(--primary-act);
}

