.section-4 {
  padding-top: 1px;
  width: 90%;
  margin: auto;
  margin-top: 100px;
  /* background-color: red; */
}
.section-4 h1 {
  font-size: 4rem;
  color: var(--primary);
  width: fit-content;
  margin-left: 35px;
}
.section-4 > div.row {
  width: 100%;
  min-height: 40vh;
  border: 1px solid gray;
  display: flex;
  justify-content: center;
  margin: 50px 0 0 0;
}
.section-4 > div.row > div:nth-child(1) h2 {
  font-size: 3rem;
  color: white;
}
.section-4 > div.row > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.section-4 > div.row > div:nth-child(1) p {
  color: gainsboro;
  font-size: 1.6rem;
}
.section-4 > div.row > div:nth-child(1) p span {
  color: white;
  font-size: 2rem;
}
.section-4 .stars i {
  font-size: 4rem;
  background-color: #00b67a;
  color: white;
  padding: 3px;
  margin-left: 5px;
}
.section-4 .trustpilot {
  padding: 5px;
  display: flex;
  align-items: center;
}
.section-4 .trustpilot i {
  font-size: 3rem;
  color: #00b67a;
}
.section-4 .trustpilot h5 {
  width: fit-content;
  color: white;
  font-size: 2rem;
}
.third-slider {
  height: 80%;
}
.clients {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 40px;
  padding-top: 15px;
}
.clients > .stars i {
  font-size: 1.9rem;
  padding: 3px;
}
.clients h2 {
  font-size: 2.5rem;
  color: white;
}
.clients p {
  color: white;
}
.clients .customer {
  position: absolute;
  bottom: 20px;
  color: gray;
  font-size: 2rem;
}
.swiper-button-next {
  font-size: 2rem;
  right: 0;
}
.swiper-button-prev {
  left: 0;
}
.find-us {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
.find-us h4 {
  font-size: 2.6rem;
  color: white;
}
.icons {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.icons span {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: white;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icons span i {
  font-size: 2.4rem;
  transition: all 0.2s;
}
.icons span:nth-child(1):hover {
  background-color: #3a5794;
  color: white;
}
.icons span:nth-child(2):hover {
  background: linear-gradient(
    45deg,
    #405de6,
    #5851db,
    #c13584,
    #e1308c,
    #fd1d1d
  );
  color: white;
}
.icons span:nth-child(3):hover {
  background-color: #27a831;
  color: white;
}
.icons span:nth-child(4):hover {
  background-color: #ff0000;
  color: white;
}
.icons span:nth-child(5):hover {
  background-color: #0077b5;
  color: white;
}
.icon-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}
.icon-logo figure {
  width: 100px;
  height: 120px;
}
.icon-logo img {
  width: 100%;
  height: 100%;
}

@media (min-width: 0px) and (max-width: 800px) {
  .section-4 {
    text-align: center;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .section-4 h1 {
    font-size: 3rem;
    margin-left: 10px !important;
  }
  .section-4 > .row > div:nth-child(2) {
    min-height: 50vh;
  }
  .section-4 > .row > div:nth-child(2) > .third-slider .clients {
    height: 100%;
  }
  .section-4 .stars i {
    font-size: 3rem;
    background-color: green;
    color: white;
    padding: 3px;
    margin-left: 5px;
  }
  .section-4 .trustpilot {
    padding: 5px;
    display: flex;
    align-items: center;
  }
  .section-4 .trustpilot i {
    font-size: 3rem;
    color: green;
  }
  .section-4 .trustpilot h5 {
    width: fit-content;
    color: white;
    font-size: 2rem;
  }
  .clients {
    display: flex;
    height: 30vh;
    flex-direction: column;
    justify-content: center !important;
    align-items: center !important;
    padding-left: 0px !important;
  }
  .clients .stars {
    padding: 0 !important;
  }
  .clients > .stars i {
    font-size: 1.9rem;
    padding: 3px;
  }
  .clients h2 {
    font-size: 2.5rem;
    color: white;
  }
  .clients p {
    color: white;
  }
  .clients .customer {
    display: none;
  }
}
