.hero-section {
  width: 100%;
  margin: auto;
  margin-top: 30px;
}
.hero-section h1 {
  width: fit-content;
  font-size: 4rem;
  color: var(--primary) !important;
  margin-left: 110px !important;
}
.swiper-container {
  width: 83.5%;
  height: 80vh;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* background: #fff; */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.hero.swiper-slide {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.hero.swiper-slide:nth-child(1) {
  background-image: url("../../../../assets/car2.png");
}
.hero.swiper-slide:nth-child(2) {
  background-image: url("../../../../assets/car1.png");
}
.hero.swiper-slide:nth-child(3) {
  background-image: url("../../../../assets/car3.png");
}
.hero.swiper-slide:nth-child(4) {
  background-image: url("../../../../assets/car4.png");
}
.hero.swiper-slide:nth-child(5) {
  background-image: url("../../../../assets/car5.png");
}
.swiper-slide button {
  font-size: 2.6rem;
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 5px 20px 5px 20px;
  background-color: var(--primary);
  transition: all 0.2s;
  position: relative;
}
.swiper-slide button:hover {
  transform: scale(1.1);
  background-color: rgb(81, 194, 156);
}
.swiper-pagination {
  width: 100px;
  height: fit-content;
  z-index: 700;
  margin-left: 600px;
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
}
.swiper-pagination-bullet {
  padding: 5px;
  border: 2px solid white;
  outline: none;
}
.swiper-pagination-bullet-active {
  background: gray;
}


@media (min-width: 0px) and (max-width: 800px) {
    .hero.swiper-container {
      height: 60vh;
      margin-top: 0px !important;
    }
    .hero-section {
      width: 84%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .hero-section > div {
      display: flex;
      justify-content: center;
    }
    .hero-section h1 {
      font-size: 2.6rem;
      margin-left: 0px !important;
    }
    .swiper-slide {
      align-items: flex-end;
      background-position: center;
      background-size: 100% 80%;
      background-repeat: no-repeat;
    }
    .swiper-container button {
      font-size: 2rem;
      margin-bottom: 15px;
    }
    .hero.swiper-pagination {
      width: 100px;
      z-index: 100;
      margin-left: 0px;
      margin-top: 365px;
      display: flex;
      z-index: 10;
      justify-content: space-evenly;
    }
  
  }