.home-section1 {
  margin-top: 60px;
  margin-left: 75px !important;
}
.home-section1 div h1 {
  color: var(--primary);
  font-size: 4rem;
}
.home-section1 div p span {
  color: var(--primary);
  font-size: 3.6rem;
}
.home-section1 div p {
  color: white;
  font-size: 2.3rem;
}
.home-section1 h4 {
  color: white;
  font-size: 3rem;
}
.home-section1 .stars i {
  font-size: 4rem;
  background-color: #00b67a;
  color: white;
  padding: 5px;
  margin-left: 5px;
}
.home-section1 .sub-section {
  border: 1px solid var(--primary);
  margin-top: 80px;
}
.trustpilot {
  padding: 5px;
  display: flex;
  align-items: center;
}
.trustpilot i {
  font-size: 3rem;
  color: #00b67a;
}
.trustpilot h5 {
  width: fit-content;
  color: white;
  font-size: 2rem;
}
.home-section1 .like,
.home-section1 .comunity {
  padding: 3px 26px 3px 26px;
  font-size: 1.7rem;
  margin-left: 8px;
  background-color: #4287f5;
  border-radius: 10px;
  transition: all 0.2;
}
.home-section1 .like i,
.home-section1 .comunity i {
  color: white;
  margin-right: 10px;
}
.home-section1 .like:hover,
.home-section1 .comunity:hover {
  transform: scale(1.05);
  background-color: rgb(108, 150, 217);
}
.home-section1 .newsletter {
  margin-top: 80px;
  margin-bottom: 50px;
}
.home-section1 .newsletter button {
  padding: 5px 30px 5px 30px;
  font-size: 2rem;
  border-radius: 20px;
  transition: all 0.2s;
}
.home-section1 .newsletter button:hover {
  background-color: var(--primary);
}

@media (min-width: 0px) and (max-width: 800px) {
  .home-section1 {
    margin-top: 75px;
    margin-left: 0px !important;
    text-align: center !important;
  }
  .home-section1 div h1 {
    color: var(--primary);
    font-size: 3.3rem;
  }
  .home-section1 div p span {
    color: var(--primary);
    font-size: 2.6rem;
  }
  .home-section1 div p {
    color: white;
    font-size: 1.9rem;
  }
  .home-section1 div p:not(.last) {
    display: flex;
    flex-direction: column;
  }
  .home-section1 > div > p:nth-child(5) {
    line-height: 1.2;
  }
  .sub-section {
    width: 95% !important;
  }
  .sub-section h4 {
    font-size: 2rem;
  }
  .sub-section h4,
  .stars {
    text-align: center;
  }
  .btn-group {
    display: flex;
    justify-content: center;
  }
  .home-section1 .like,
  .home-section1 .comunity {
    padding: 8px 10px 8px 10px !important;
    font-size: 1.2rem;
    background-color: #4287f5;
    border-radius: 10px;
  }
  .home-section1 .like i,
  .home-section1 .comunity i {
    color: white;
    margin-right: 5px;
  }
  .home-section1 .newsletter {
    width: 100% !important;
    margin-top: 40px;
  }
  .home-section1 .newsletter button {
    padding: 5px 30px 5px 30px;
    font-size: 1.5rem;
    border-radius: 20px;
  }
}