
.section-3 {
    padding-top: 1px;
    width: 87%;
    margin: auto;
    margin-top: 100px;
  }
  .section-3 h1 {
    font-size: 4rem;
    color: var(--primary);
    width: fit-content;
  }
  .section-3 .swiper-container {
    width: 100%;
    height: 60vh;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
  }
  .second.swiper-slide > div.row {
    width: 100%;
    height: 100%;
  }
  .second.swiper-slide > div.row > div:nth-child(1) {
    width: 50%;
    background-image: url(../../../../../assets/game1.jpg);
    background-position: center;
    background-size: cover;
  }
  .second.swiper-slide:nth-child(1) > div.row > div:nth-child(1) {
    background-image: url(../../../../../assets/game1.jpg);
  }
  .second.swiper-slide:nth-child(2) > div.row > div:nth-child(1) {
    background-image: url(../../../../../assets/77217552.webp);
  }
  .second.swiper-slide:nth-child(3) > div.row > div:nth-child(1) {
    background-image: url(../../../../../assets/game2.jpg);
  }
  .second.swiper-slide:nth-child(4) > div.row > div:nth-child(1) {
    background-image: url(../../../../../assets/51fBODtSLnL._AC_SX466_.jpg);
  }
  .second.swiper-slide:nth-child(5) > div.row > div:nth-child(1) {
    background-image: url(../../../../../assets/XBSS-mb.webp);
  }
  .second.swiper-slide > div.row > div:nth-child(2) {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 30px;
  }
  .second.swiper-slide > div.row > div:nth-child(2) figure {
    width: 100px;
    height: 100px;
    margin-left: 30px;
  }
  .second.swiper-slide > div.row > div:nth-child(2) figure img {
    width: 100%;
    height: 100%;
  }
  .second.swiper-slide > div.row > div:nth-child(2) h3 {
    color: white;
    font-size: 2.4rem;
  }
  .second.swiper-slide > div.row > div:nth-child(2) h4 {
    color: white;
    font-size: 1.8rem;
  }
  .second.swiper-slide > div.row > div:nth-child(2) p {
    color: white;
    font-size: 2rem;
  }
  
  .second.swiper-slide > div.row > div:nth-child(2) h5 {
    color: white;
    font-size: 1.6rem;
  }
  .loveit {
    margin-top: 80px !important;
    font-size: 2.5rem !important;
    /* color: white !important; */
    color: gainsboro !important;
  }

  @media (min-width:0px) and (max-width:800px)
  {
    .section-3 {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 60px;
      text-align: center;
    }
    .section-3 .swiper-container {
      height: 100vh !important;
    }
    .section-3 > h1 {
      font-size: 3rem;
    }
    .second.swiper-pagination {
      width: 100px;
      z-index: 100;
      margin-left: 20px;
      margin-top: 550px;
      display: flex;
      z-index: 10;
      justify-content: space-evenly;
    }
    .second.swiper-slide > div.row > div:nth-child(2) figure {
      margin: 0;
    }
    .second.swiper-slide > div.row > div:nth-child(2) {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 0;
    }
    .second.swiper-slide > div.row > div:nth-child(2) div {
      position: absolute;
      bottom: 23%;
      text-align: center;
    }
    .loveit {
      font-size: 2.2rem !important;
      margin-top: 20px !important;
    }
  }