.footer {
  padding-top: 1px;
  width: 90%;
  margin: auto;
  margin-top: 80px;
  /* background-color: red; */
}
.footer h1 {
  font-size: 4rem;
  color: var(--primary);
  width: fit-content;
  margin-left: 35px;
}
.giftshop {
  width: 100%;
  min-height: 40vh;
  border: 1px solid gray;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px 0 0 0;
}
.giftshop > div {
  width: 25%;
  height: 35vh;
  transition: all .7;
}
.giftshop video {
  width: 100%;
  height: 100%;
  outline: none;
}
.giftshop figure {
  width: 100%;
  height: 100%;
}
.giftshop figure img {
  width: 100%;
  height: 100%;
}
.giftshop>div:hover
{
  transform: scale(1.07);
}
.footer > .btnn {
  display: flex;
  justify-content: center;
  padding: 60px 0;
}
.footer > .btnn > button {
  font-size: 2rem;
  padding: 5px 120px 5px 120px;
  border-radius: 20px;
  transition: all 0.2s;
}
.footer > .btnn > button:hover {
  background-color: var(--primary);
}
.footer .payment-method {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 10px;
}
.footer .payment-method h3 {
  font-size: 2.6rem;
  color: white;
}
.footer .payment-method div {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.footer .payment-method div figure {
  width: 80px;
  height: 40px;
  margin-left: 10px;
}
.footer .payment-method div figure img {
  width: 100%;
  height: 100%;
}
.copyright {
  margin-top: 50px;
  display: flex;
  justify-content: space-evenly;
  color: white;
}
.copyright p {
  font-size: 1.7rem;
  transition: all 0.2s;
}
.copyright p a {
  color: white;
}
.copyright p:hover,
.copyright p a:hover {
  color: var(--primary);
}

@media (min-width: 0px) and (max-width: 800px) {
  .footer {
    margin-top: 50px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer h1 {
    font-size: 3.3rem;
    margin-left: 12px;
  }
  .footer > .btnn > button {
    padding: 5px 80px 5px 80px;
  }
  .footer .payment-method h3 {
    font-size: 2.4rem;
  }
  .footer .payment-method div figure {
    width: 70px;
    margin: 0 0 0 5px;
  }
  .copyright {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .copyright p {
    font-size: 1.6rem;
  }
  .giftshop {
    flex-direction: column;
    justify-content: space-evenly;
  }
  .giftshop > div {
    width: 80%;
    margin-bottom: 70px;
  }
}
