.section-2 {
  width: 98%;
  margin: 0 auto;
  margin-top: 80px;
}


.section-2 > h1 {
  font-size: 4rem;
  color: var(--primary);
  margin-left: 70px;
}
.items-container {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 40px;
}
.items {
  width: 22%;
  height: 45vh;
  /* border: 6px solid #fdf9ff; */
  margin-top: 30px;
  margin-left: 30px;
  border-radius: 10px;
  position: relative;
  transition: all .5s;
}
.section-2 .items:hover
{
  transform: scale(1.07);
}
.items > div {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
  background-position: center;
  background-size: cover;
  border-radius: 5px;
  position: relative;
}
.items:nth-child(1) > div {
  background-image: url(../../../../../assets/XBSS-mb.webp);
}
.items:nth-child(2) > div {
  background-image: url(../../../../../assets/puma-gaming-sock-hed-page-2019.png);
}
.items:nth-child(3) > div {
  background-image: url(../../../../../assets/game1.jpg);
}
.items:nth-child(4) > div {
  background-image: url(../../../../../assets/game2.jpg);
}
.items:nth-child(5) > div {
  background-image: url(../../../../../assets/51fBODtSLnL._AC_SX466_.jpg);
}
.items:nth-child(6) > div {
  background-image: url(../../../../../assets/20d87417485084d29f2af1773b9aebfa.jpg);
}
.items:nth-child(7) > div {
  background-image: url(../../../../../assets/1660-gaming-1-4012-249095-290519041917.jpg);
}
.items:nth-child(8) > div {
  background-image: url(../../../../../assets/ASUS-Strix-Surround-Sound-Gaming-Headset-680x546.jpg);
}
.items:nth-child(9) > div {
  background-image: url(../../../../../assets/images.jpg);
}
.items:nth-child(10) > div {
  background-image: url(../../../../../assets/77217552.webp);
}
.sold-out {
  width: 95%;
  height: 30px;
  background-color: var(--primary);
  transform: rotate(-19deg);
  position: absolute;
  top: 70px;
  left: 0;
  text-align: center;
  /* padding-top: 5px; */
  font-size: 2rem;
}
.items-colorfull {
  width: 90%;
  height: 16px;
  border-radius: 10px;
  border: 1px solid white;
  margin-bottom: 15px;
  position: relative;
}
.items-colorfull::before {
  content: "";
  position: absolute;
  width: 80%;
  border-radius: 10px 0px 0 10px;
  height: 100%;
  background-color: var(--primary);
}
.items:nth-child(2) .items-colorfull::before {
  width: 70%;
  background-color: var(--primary);
}
.items:nth-child(3) .items-colorfull::before {
  width: 20%;
  background-color: var(--primary);
}
.items:nth-child(4) .items-colorfull::before {
  width: 100%;
  border-radius: 10px;
  background-color: var(--primary);
}
.items:nth-child(5) .items-colorfull::before {
  width: 60%;
  background-color: var(--primary);
}
.items:nth-child(6) .items-colorfull::before {
  width: 40%;
  background-color: var(--primary);
}
.items:nth-child(7) .items-colorfull::before {
  width: 80%;
  background-color: var(--primary);
}
.items:nth-child(8) .items-colorfull::before {
  width: 12%;
  background-color: var(--primary);
}
.items:nth-child(9) .items-colorfull::before {
  width: 80%;
  background-color: var(--primary);
}
.items:nth-child(10) .items-colorfull::before {
  width: 80%;
  background-color: var(--primary);
}

.items > div {
  width: 100%;
  height: 70%;
}
.items h3 {
  font-size: 1.5rem;
  color: white;
  margin-top: 10px;
  padding-left: 10px;
}
.items h1 {
  padding-left: 10px;
  font-size: 2.7rem;
  color: var(--primary);
}

@media (min-width: 0px) and (max-width: 800px) {
  .section-2 {
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    align-items: center;
  }
  .section-2 > h1 {
    font-size: 3.3rem;
    margin-left: 10px;
  }
}

@media (min-width: 900px) and (max-width: 1100px) {
    .items {
      flex: 0 0 30%;
      max-width: 30%;
    }
  }
  @media (min-width: 500px) and (max-width: 850px) {
    .items {
      flex: 0 0 40%;
      max-width: 40%;
    }
  }
  
  @media (max-width: 499px) {
    .items {
      flex: 0 0 80%;
      max-width: 80%;
    }
  }
